import * as React from 'react';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import { Container } from '@mui/system';
import { Home, Search, ShoppingBag, VideoFile } from '@mui/icons-material';
import { Avatar } from '@mui/joy';

export default function LabelBottomNavigation() {
  const [value, setValue] = React.useState('home');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Container sx={{position:'sticky',bottom:0}}>
    <BottomNavigation sx={{ justifyContent:'space-between', postion:'sticky'}} value={value} onChange={handleChange}>
      <BottomNavigationAction
        label=""
        value="home"
        icon={<Home/>}
      />
      <BottomNavigationAction
        label=""
        value="search"
        icon={<Search />}
      />
      <BottomNavigationAction
        label="Nearby"
        value="shorts"
        icon={<VideoFile />}
      />
      <BottomNavigationAction 
      label="Folder" 
      value="shopping" 
      icon={<ShoppingBag />} />
      <BottomNavigationAction 
      label="" 
      value="Profile" 
      icon={<Avatar />} />
    </BottomNavigation>
    
    </Container>
  );
}
